* {
  color: white;
  font-weight: 400;
}

.home-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.home-wrapper {
  padding: 10px;
  word-spacing: 2px;
}

.home-wrapper > h1 {
  font-size: 2rem;
  text-align: center;
}

.home-wrapper > h2 {
  text-align: center;
  font-size: 1.2em;
  line-height: 2rem;
}

.name-span {
  font-size: 2rem;
}

.home-wrapper > h2 > span {
  font-size: 2rem;
}

.home-wrapper > p {
  text-align: justify;
  word-spacing: 0.5px;
  font-size: 14px;
  font-family: sans-serif;
  padding-top: 20px;
}

.contactBtn {
  width: 150px;
  height: 50px;
  border-radius: 10px;
  color: black;
  border: none;
  box-shadow: -1px 1px 20px purple;
}
