.app-container {
  width: 100%;
  height: 100%;
  isolation: isolate;
  position: relative;
}

.bgVideo {
  min-width: 375px;
  min-height: 100%;
  z-index: -1;
  top: 0;
  left: -1000px;
  position: fixed;
}
video {
  min-width: 100%;
  min-height: 100%;
}

.bgVideo::after {
  content: "";
  position: absolute;
  background-color: rgba(5, 5, 5, 0.93);
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
}
